<template>
    <div class="request-password-reset">
        <h1>  </h1>
        <form @submit.prevent="requestResetLink">
            <div>
                <label for="email">:</label>
                <input type="email" id="email" v-model="email" placeholder=" " required />
            </div>
            <button type="submit">   </button>
        </form>
        <p v-if="message" :class="success ? 'success' : 'error'">{{ message }}</p>
    </div>
</template>

<script>
export default {
    data() {
        return {
            email: '',
            message: '',
            success: false,
        };
    },
    methods: {
        async requestResetLink() {
            try {
                const response = await fetch(`${process.env.VUE_APP_API_URL}/request-password-reset`, {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ email: this.email }),
                });

                const result = await response.json();
                this.success = result.success;
                this.message = result.message;
            } catch (error) {
                console.error('Error requesting password reset:', error);
                this.success = false;
                this.message = '     .';
            }
        },
    },
};
</script>

<style scoped>
.success {
    color: green;
}

.error {
    color: red;
}
</style>