<template>
  <div class="contents">
    <!-- [1]  (//  )  -->
    <section class="contents-header" v-if="currentPage === 'board'">
      <div class="header-left">
        <!--    -->
        <div class="select-wrapper">
          <select id="sort" v-model="sortOrder" @change="sortPosts" class="filter-dropdown">
            <option value="latest"></option>
            <option value="oldest"></option>
            <option value="likes"></option>
            <option value="views"></option>
          </select>
        </div>

        <!--    -->
        <div class="select-wrapper">
          <select v-model="searchType" class="filter-dropdown">
            <option value="title"></option>
            <option value="content"></option>
            <option value="author"></option>
          </select>
        </div>

        <!--    -->
        <button @click="resetSearch" class="reset-button"></button>
      </div>

      <div class="header-right">
        <!--   -->
        <div class="search-box">
          <input v-model="searchQuery" @keyup.enter="filterPosts" type="text" placeholder=" "
            class="search-input" />
          <span class="search-icon" @click="filterPosts"></span>
        </div>
      </div>
    </section>

    <!--     -->
    <div class="write-button-container" v-if="currentPage === 'board'">
      <button @click="goToWritePage" class="write-button"> </button>
    </div>

    <!--   -->
    <div v-if="currentPage === 'board'" class="feed-container">
      <!-- //    -->
      <div v-if="loading" class="loading"> ...</div>
      <div v-else-if="error" class="error">   .</div>

      <div v-else-if="sortedPosts.length" class="feed-list">
        <div v-for="post in sortedPosts" :key="post._id" class="feed-card">
          <div class="feed-header">
            <strong>{{ post.author || ' ' }}</strong>
            <div>{{ formatDate(post.createdAt) }}</div>
          </div>
          <h2 class="feed-title" @click="goToDetailPage(post._id)">{{ post.title }}</h2>
          <p class="feed-content" v-html="convertNewLinesToBreaks(post.content)"></p>

          <div v-if="post.imageUrl" class="feed-image">
            <img :src="`http://localhost:3000${post.imageUrl}`" alt=" " />
          </div>

          <div class="feed-actions">
            <span> {{ post.likes || 0 }}</span>
            <span style="margin-left: 10px;"> {{ post.dislikes || 0 }}</span>
            <span class="view-count" style="margin-left: 10px;">: {{ post.views || 0 }}</span>
          </div>
        </div>
      </div>

      <div v-else class="no-posts"> .</div>
    </div>

    <!--    -->
    <div v-if="currentPage === 'write'" class="post-form">
      <h2 class="post-title"> </h2>
      <form @submit.prevent="submitPost">
        <input v-model="title" type="text" placeholder=" " class="post-input" required />

        <textarea v-model="content" placeholder=" " class="post-textarea" required></textarea>

        <input type="file" @change="handleImageUpload" accept="image/*" />

        <div class="post-buttons">
          <button type="submit" class="post-submit"> </button>
          <button type="button" @click="goToBoardPage" class="post-cancel"></button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      posts: [],          //  
      filteredPosts: [],  //   
      loading: true,      //  
      error: false,       //  
      title: '',          //  
      content: '',        //  
      currentPage: 'board',   //    ('board', 'write')
      currentUser: null,      //    
      sortOrder: 'latest',    //  
      searchQuery: '',        // 
      searchType: 'title',    //   (, , )
      image: null, // 
    };
  },
  computed: {
    //       
    sortedPosts() {
      return [...this.filteredPosts].sort((a, b) => {
        if (this.sortOrder === 'latest') {
          return new Date(b.createdAt) - new Date(a.createdAt); // 
        } else if (this.sortOrder === 'oldest') {
          return new Date(a.createdAt) - new Date(b.createdAt); // 
        } else if (this.sortOrder === 'likes') {
          return (b.likes || 0) - (a.likes || 0);               // 
        } else if (this.sortOrder === 'views') {
          return (b.views || 0) - (a.views || 0);               // 
        }
      });
    }
  },
  created() {
    this.initData();        //   
    this.checkLoginStatus(); //   
  },
  methods: {
    handleImageUpload(event) {
      this.image = event.target.files[0];
    },

    //   
    async checkLoginStatus() {
      try {
        const response = await axios.get(`${process.env.VUE_APP_API_URL}/auth/check-login`, {
          withCredentials: true
        });
        this.currentUser = response.data.user || null; //   
      } catch (error) {
        console.error('     :', error);
        this.currentUser = null; //  
      }
    },

    //   
    async initData() {
      this.loading = true;
      this.error = false;
      try {
        await this.fetchPosts();
      } catch (error) {
        console.error('     :', error);
        this.error = true;
      } finally {
        this.loading = false;
      }
    },

    //   
    async fetchPosts() {
      try {
        const response = await axios.get(`${process.env.VUE_APP_API_URL}/api/board`, {
          withCredentials: true
        });
        this.posts = response.data;
        this.filteredPosts = [...this.posts];
      } catch (error) {
        console.error('    :', error);
        this.error = true;
      }
    },

    //  
    filterPosts() {
      if (this.searchQuery.trim()) {
        //  
        if (this.searchType === 'title') {
          this.filteredPosts = this.posts.filter(post =>
            post.title.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        }
        //  
        else if (this.searchType === 'content') {
          this.filteredPosts = this.posts.filter(post =>
            post.content.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        }
        //  
        else if (this.searchType === 'author') {
          this.filteredPosts = this.posts.filter(post =>
            (post.author || '').toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        }
      } else {
        this.filteredPosts = [...this.posts]; //    
      }
    },

    //  
    async submitPost() {
      const formData = new FormData();
      formData.append('title', this.title);
      formData.append('content', this.content);
      if (this.image) {
        formData.append('image', this.image);
      }

      try {
        await axios.post(`${process.env.VUE_APP_API_URL}/api/board`, formData, {
          withCredentials: true,
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
        this.goToBoardPage();
        this.initData();
      } catch (error) {
        console.error('    :', error);
      }
    },

    //  
    goToBoardPage() {
      this.currentPage = 'board';
      this.title = '';
      this.content = '';
    },
    goToWritePage() {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      this.currentPage = 'write';
    },
    goToDetailPage(postId) {
      this.$router.push({ name: 'BoardDetail', params: { id: postId } });
    },

    //   
    formatDate(dateString) {
      return new Date(dateString).toLocaleDateString(undefined, {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },

    //  
    resetSearch() {
      this.searchQuery = '';
      this.filteredPosts = [...this.posts];
    },

    //   -> <br> 
    convertNewLinesToBreaks(text) {
      return text.replace(/\n/g, '<br>');
    }
  }
};
</script>

