<template>
  <div class="contents">
    <!-- 1:1   ( ) -->
    <section class="contents-item">
      <p class="section-title">1:1 </p>
      <button @click="goToForm" class="write-btn"> </button>
      <div v-if="inquiries.length === 0">  .</div>
      <div v-else class="feed-list">
        <div v-for="inquiry in inquiries" :key="inquiry._id" class="feed-card" @click="goToDetail(inquiry._id)">
          <h3>{{ inquiry.title }}</h3>
          <p>: {{ inquiry.name }}</p>
          <p>: {{ inquiry.status === 'answered' ? '' : '' }}</p>
          <p>: {{ formatDate(inquiry.createdAt) }}</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'InquiryList',
  data() {
    return {
      inquiries: [],
    };
  },
  async created() {
    await this.fetchInquiries();
  },
  methods: {
    async fetchInquiries() {
      try {
        const res = await axios.get(`${process.env.VUE_APP_API_URL}/api/inquiries`, {
          withCredentials: true,
        });
        this.inquiries = res.data.inquiries;
      } catch (error) {
        console.error('   :', error);
      }
    },
    goToForm() {
      this.$router.push('/inquiries/new');
    },
    goToDetail(id) {
      this.$router.push(`/inquiries/${id}`);
    },
    formatDate(dateStr) {
      const date = new Date(dateStr);
      return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
    },
  },
};
</script>

