<template>
  <div class="inquiry-form">
    <h2> </h2>
    <form @submit.prevent="submitInquiry">
      <div>
        <label for="title"></label>
        <input v-model="title" id="title" required />
      </div>
      <div>
        <label for="content"></label>
        <textarea v-model="content" id="content" rows="6" required></textarea>
      </div>
      <button type="submit"></button>
    </form>

    <p v-if="message">{{ message }}</p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'InquiryForm',
  data() {
    return {
      title: '',
      content: '',
      message: '',
    };
  },
  methods: {
    async submitInquiry() {
      try {
        await axios.post(
          `${process.env.VUE_APP_API_URL}/api/inquiries`,
          {
            title: this.title,
            content: this.content,
          },
          { withCredentials: true }
        );
        this.message = '  !';
        this.title = '';
        this.content = '';
        this.$router.push('/inquiries');
      } catch (error) {
        console.error('  :', error);
        this.message = '    .';
      }
    },
  },
};
</script>

<style scoped>
.inquiry-form {
  max-width: 600px;
  margin: 0 auto;
}

label {
  display: block;
  margin-top: 1rem;
}

input,
textarea {
  width: 100%;
  padding: 0.5rem;
  margin-top: 0.25rem;
}

button {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
}
</style>