<script>
export default {
  name: 'App',
  data() {
    return {
      mypageopen: false,
      isLoggedIn: false, //  
      userInfo: {},
      windowWidth: window.innerWidth,
      lastetVersion: "",
    };
  },


  computed: {
    // ,    
    isAuthPage() {
      return ['/login', '/signup', '/find-password', '/find-id', '/mypage-edit', '/matchqueue', '/chatroom', '/board', '/patch-notes'].includes(this.$route.path);
    },
    //    
    isMobile() {
      return this.windowWidth < 768; // : 768px    
    },
    showHeader() {
      return !this.isAuthPage;
    },
    showFooter() {
      return !this.isAuthPage;
    },
    //     
    showPlaceholder() {
      return !this.isAuthPage;
    },
    videoElement() {
      return this.$refs.videoElement;
    }
  },
  methods: {
    handleResize() {
      this.windowWidth = window.innerWidth; //      
    },
    enterMatchQueue() {
      this.$router.push('/matchqueue');
    },
    playVideo() {
      this.videoElement.play();
    },
    pauseVideo() {
      this.videoElement.pause();
    },
    async fetchLatestVersion() {
      try {
        const response = await fetch("https://ddragon.leagueoflegends.com/api/versions.json");
        const versions = await response.json();
        this.latestVersion = versions[0]; //    
      } catch (error) {
        console.error("Failed to fetch Data Dragon version:", error);
      }
    },
    async checkLoginStatus() {

      try {
        const response = await fetch(`${process.env.VUE_APP_API_URL}/auth/check-login`, {
          method: 'GET',
          credentials: 'include', //  
        });

        if (response.ok) {
          const data = await response.json();
          this.isLoggedIn = data.loggedIn;
          if (data.loggedIn) {
            console.log("  :", data.user);
            this.userInfo = data.user || {}; //    
          }
        } else {
          this.resetUserData();
        }
      } catch (error) {
        console.error('Error checking login status:', error);
        this.resetUserData();
      }
    },
    resetUserData() {
      this.isLoggedIn = false;
      this.userInfo = {}; //   
    },
    async logout() {
      try {
        const response = await fetch(`${process.env.VUE_APP_API_URL}/logout`, {
          method: 'GET',
          credentials: 'include',
        });

        if (response.ok) {
          this.isLoggedIn = false;
          this.nickname = '';
          this.$router.push('/'); //     
        }
      } catch (error) {
        console.error('Error logging out:', error);
      }
    },
    async withdraw() {
      const confirmation = confirm('  ?');
      if (!confirmation) return;

      try {
        const response = await fetch(`${process.env.VUE_APP_API_URL}/withdraw`, {
          method: 'POST',
          credentials: 'include',
        });

        if (response.ok) {
          alert(' .');
          this.resetUserData();
          this.$router.push('/'); //   
        } else {
          alert('   .  .');
        }
      } catch (error) {
        console.error('Error withdrawing: ', error);
        alert('   .  .');
      }
    },
  },
  mounted() {
    window.addEventListener('resize', this.handleResize); //     
    this.checkLoginStatus(); //      
    this.fetchLatestVersion();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize); //  
  },
};
</script>

