<template>
    <div class="reset-password">
        <h1> </h1>
        <form @submit.prevent="resetPassword">
            <div>
                <label for="newPassword"> :</label>
                <input type="password" id="newPassword" v-model="newPassword" placeholder="  " required />
            </div>
            <div>
                <label for="confirmPassword"> :</label>
                <input type="password" id="confirmPassword" v-model="confirmPassword" placeholder="  "
                    required />
            </div>
            <button type="submit"> </button>
        </form>
        <p v-if="message" :class="success ? 'success' : 'error'">{{ message }}</p>
    </div>
</template>

<script>
export default {
    data() {
        return {
            newPassword: '',
            confirmPassword: '',
            message: '',
            success: false,
        };
    },
    methods: {
        async resetPassword() {
            const token = new URLSearchParams(window.location.search).get('token');
            if (!token) {
                this.success = false;
                this.message = '  .';
                return;
            }

            if (this.newPassword !== this.confirmPassword) {
                this.success = false;
                this.message = '  .';
                return;
            }

            try {
                const response = await fetch(`${process.env.VUE_APP_API_URL}/reset-password`, {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ token, newPassword: this.newPassword }),
                });

                const result = await response.json();
                this.success = result.success;
                this.message = result.message;
            } catch (error) {
                console.error('Error resetting password:', error);
                this.success = false;
                this.message = '    .';
            }
        },
    },
};
</script>

<style scoped>
.success {
    color: green;
}

.error {
    color: red;
}
</style>