<template>
  <div class="post-container">
    <!-- [1]   /  -->
    <div v-if="loading" class="loading">
      <h2> ...</h2>
    </div>
    <div v-else-if="!post" class="error">
      <h2>  .</h2>
    </div>

    <!-- [2]    -->
    <div v-else class="post-card">
      <!-- [2-1]    -->
      <div v-if="isEditing">
        <h2> </h2>
        <form @submit.prevent="updatePost" class="edit-form">
          <input v-model="editedTitle" type="text" placeholder=" " required class="input-field" />
          <textarea v-model="editedContent" placeholder=" " required class="textarea-field"></textarea>

          <!--     -->
          <input type="file" @change="handleEditImageUpload" accept="image/*" />

          <div class="form-buttons">
            <button type="submit" class="save-btn"> </button>
            <button type="button" @click="cancelEdit" class="cancel-btn"></button>
          </div>
        </form>
      </div>

      <!-- [2-2]     -->
      <div v-else>
        <!-- //  -->
        <div class="post-header">
          <h1>{{ post.title }}</h1>
          <div class="post-meta">
            <span>: <strong>{{ post.author || '' }}</strong></span>
            <span>: {{ formatDate(post.createdAt) }}</span>
            <span>: {{ post.views || 0 }}</span>
          </div>
        </div>

        <!--   -->
        <p class="post-content" v-html="convertNewLinesToBreaks(post.content)"></p>

        <!--     -->
        <div v-if="post.imageUrl" class="post-image">
          <img :src="`${process.env.VUE_APP_API_URL}${post.imageUrl}?t=${new Date().getTime()}`" alt=" " />
        </div>

        <!-- / -->
        <div class="action-buttons">
          <button @click="likePost" class="like-btn">
              ({{ post.likes }})
          </button>
          <button @click="dislikePost" class="like-btn" style="margin-left:8px;">
              ({{ post.dislikes }})
          </button>
        </div>

        <!--  /  ( ) -->
        <div v-if="isAuthor" class="edit-actions">
          <button @click="enterEditMode" class="edit-btn"> </button>
          <button @click="deletePost" class="delete-btn"> </button>
        </div>
      </div>

      <!-- [3]   -->
      <div class="comments-section" v-if="!isEditing">
        <h3> ({{ comments.length }})</h3>

        <!--    ( 1   ) -->
        <div v-if="comments.length > 0" style="margin-bottom: 15px;">
          <label for="comment-sort" style="margin-right:6px;"> :</label>
          <select id="comment-sort" v-model="sortOrder" @change="sortComments"
            style="border-radius:5px; background:#444; color:white; border:none; padding:4px 8px;">
            <option value="latest"></option>
            <option value="oldest"></option>
            <option value="likes"></option>
          </select>
        </div>

        <!--    (  &     ) -->
        <div v-if="currentUser && !isEditing" class="comment-input">
          <textarea v-model="newComment" placeholder=" "></textarea>
          <button @click="submitComment" class="comment-submit"> </button>
        </div>

        <!--   -->
        <ul class="comment-list">
          <li v-for="comment in sortedComments" :key="comment._id" class="comment-item">
            <div class="comment-header">
              <strong>{{ comment.nickname }}</strong>
              <span> | {{ formatDate(comment.createdAt) }}</span>
            </div>

            <!--    -->
            <div v-if="editingCommentId === comment._id">
              <textarea v-model="editingContent" class="textarea-field"></textarea>
              <div class="comment-actions">
                <button @click="saveEditedComment(comment._id)" class="save-btn">
                  
                </button>
                <button @click="cancelEditing" class="cancel-btn"></button>
              </div>
            </div>

            <!--    -->
            <div v-else>
              <p v-html="convertNewLinesToBreaks(comment.content)"></p>
              <div class="comment-actions">
                <!-- / -->
                <button @click="likeComment(comment._id)" class="like-btn">
                   ({{ comment.likes || 0 }})
                </button>
                <button @click="dislikeComment(comment._id)" class="like-btn" style="margin-left:8px;">
                   ({{ comment.dislikes || 0 }})
                </button>

                <!--  /  ( ) -->
                <div v-if="currentUser?.userid === comment.userId" style="display:inline-block; margin-left:10px;">
                  <button @click="startEditingComment(comment._id, comment.content)" class="edit-btn"> </button>
                  <button @click="deleteComment(comment._id)" class="delete-btn"> </button>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  props: ['id'], //  params   ID
  data() {
    return {
      post: null,            //  
      currentUser: null,     //   
      loading: true,         //  
      isEditing: false,      //   
      editedTitle: '',       //   
      editedContent: '',     //   
      comments: [],          //  
      newComment: '',        //   
      editingCommentId: null,//    ID
      editingContent: '',    //    
      sortOrder: 'latest',   //   
      editedImage: null, //      
    };
  },
  computed: {
    //    
    isAuthor() {
      return this.currentUser?.nickname === this.post?.author;
    },
    //   
    sortedComments() {
      return [...this.comments].sort((a, b) => {
        if (this.sortOrder === 'latest') {
          return new Date(b.createdAt) - new Date(a.createdAt); // 
        } else if (this.sortOrder === 'oldest') {
          return new Date(a.createdAt) - new Date(b.createdAt); // 
        } else if (this.sortOrder === 'likes') {
          return (b.likes || 0) - (a.likes || 0);               // 
        }
      });
    },
  },
  methods: {
    //   
    async loadData() {
      this.loading = true;
      try {
        // , ,    
        await Promise.all([this.fetchPost(), this.fetchComments(), this.fetchCurrentUser()]);
      } catch (error) {
        console.error('    :', error);
      } finally {
        this.loading = false;
      }
    },
    //  
    async fetchPost() {
      try {
        const response = await axios.get(`${process.env.VUE_APP_API_URL}/api/board/${this.id}`, {
          withCredentials: true
        });
        this.post = response.data;
        this.editedTitle = this.post.title;
        this.editedContent = this.post.content;
      } catch (error) {
        console.error('    :', error);
        this.post = null;
      }
    },
    //  
    async fetchComments() {
      try {
        const response = await axios.get(`${process.env.VUE_APP_API_URL}/api/board/${this.id}/comments`, {
          withCredentials: true
        });
        this.comments = response.data;
      } catch (error) {
        console.error('    :', error);
      }
    },
    //    
    async fetchCurrentUser() {
      try {
        const response = await axios.get(`${process.env.VUE_APP_API_URL}/auth/check-login`, {
          withCredentials: true
        });
        if (response.data.loggedIn) {
          this.currentUser = response.data.user;
        }
      } catch (error) {
        console.error('     :', error);
        this.currentUser = null;
      }
    },
    //  
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString('ko-KR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit',
      });
    },
    //   
    enterEditMode() {
      this.isEditing = true;
    },
    //   
    cancelEdit() {
      this.isEditing = false;
      //   
      this.editedTitle = this.post.title;
      this.editedContent = this.post.content;
    },
    //   
    async updatePost() {
      try {
        if (this.editedImage) {
          //   , FormData  
          const formData = new FormData();
          formData.append('title', this.editedTitle);
          formData.append('content', this.editedContent);
          formData.append('image', this.editedImage);
          await axios.put(`${process.env.VUE_APP_API_URL}/api/board/${this.id}`, formData, {
            withCredentials: true,
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          });
        } else {
          //     
          await axios.put(
            `${process.env.VUE_APP_API_URL}/api/board/${this.id}`,
            { title: this.editedTitle, content: this.editedContent },
            { withCredentials: true }
          );
        }
        //          
        await this.fetchPost();
        this.isEditing = false;
        alert(' .');
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //  
    async deletePost() {
      try {
        await axios.delete(`${process.env.VUE_APP_API_URL}/api/board/${this.id}`, {
          withCredentials: true
        });
        alert(' .');
        this.$router.push('/board');
      } catch (error) {
        console.error('    :', error);
      }
    },
    //  
    async likePost() {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      try {
        const response = await axios.put(
          `${process.env.VUE_APP_API_URL}/api/board/${this.id}/like`,
          { action: 'like' },
          { withCredentials: true }
        );
        this.post.likes = response.data.likes;
        this.post.dislikes = response.data.dislikes;
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //  
    async dislikePost() {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      try {
        const response = await axios.put(
          `${process.env.VUE_APP_API_URL}/api/board/${this.id}/like`,
          { action: 'dislike' },
          { withCredentials: true }
        );
        this.post.likes = response.data.likes;
        this.post.dislikes = response.data.dislikes;
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //  
    async submitComment() {
      if (!this.newComment.trim()) {
        alert('  .');
        return;
      }
      try {
        const response = await axios.post(
          `${process.env.VUE_APP_API_URL}/api/board/${this.id}/comments`,
          { content: this.newComment },
          { withCredentials: true }
        );
        this.comments.push(response.data);
        this.newComment = '';
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //   
    startEditingComment(commentId, content) {
      this.editingCommentId = commentId;
      this.editingContent = content;
    },
    //   
    cancelEditing() {
      this.editingCommentId = null;
      this.editingContent = '';
    },
    //   
    async saveEditedComment(commentId) {
      if (!this.editingContent.trim()) {
        alert('  .');
        return;
      }
      try {
        await axios.put(
          `${process.env.VUE_APP_API_URL}/api/board/comments/${commentId}`,
          { content: this.editingContent },
          { withCredentials: true }
        );
        const comment = this.comments.find(c => c._id === commentId);
        if (comment) {
          comment.content = this.editingContent;
        }
        this.cancelEditing();
        alert(' .');
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //  
    async deleteComment(commentId) {
      try {
        await axios.delete(`${process.env.VUE_APP_API_URL}/api/board/comments/${commentId}`, {
          withCredentials: true
        });
        this.comments = this.comments.filter(comment => comment._id !== commentId);
        alert(' .');
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //  
    async likeComment(commentId) {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      try {
        await axios.put(
          `${process.env.VUE_APP_API_URL}/api/board/comments/${commentId}/like`,
          { action: 'like' },
          { withCredentials: true }
        );
        await this.fetchComments(); // 
      } catch (error) {
        console.error('     :', error);
      }
    },
    //  
    async dislikeComment(commentId) {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      try {
        await axios.put(
          `${process.env.VUE_APP_API_URL}/api/board/comments/${commentId}/like`,
          { action: 'dislike' },
          { withCredentials: true }
        );
        await this.fetchComments(); // 
      } catch (error) {
        console.error('     :', error);
      }
    },
    //    (    )
    sortComments() {
      console.log(`  ${this.sortOrder} .`);
    },
    //  
    async incrementViews() {
      try {
        const response = await axios.post(
          `${process.env.VUE_APP_API_URL}/api/board/${this.id}/views`,
          {},
          { withCredentials: true }
        );
        if (response.status === 200) {
          this.post.views += 1;
        }
      } catch (error) {
        console.error('     :', error);
      }
    },
    //   -> <br> 
    convertNewLinesToBreaks(text) {
      return text.replace(/\n/g, '<br>');
    },
    handleEditImageUpload(event) {
      this.editedImage = event.target.files[0];
    },
  },
  created() {
    //      ->  
    this.loadData().then(() => {
      this.incrementViews();
    });
  },
};
</script>

